// From Hologram Samples
code{
	padding: 2px 4px;
	color: #d14;
	white-space: nowrap;
	background-color: #f9f9f9;
	border: 1px solid #ccc;
	border-radius: 4px;
}

.codeExample,
.jsExample{
	border: 1px solid #ccc;
	border-radius: 4px;
	margin: 10px 0;
	&:before{
		font-family: "OpenSans", sans-serif;
		color: #222;
		border: 1px solid #ccc;
		border-radius: 4px;
		border-bottom-left-radius: 0;
		border-top-right-radius: 0;
		position: relative;
		padding: 2px;
		display: block;
		font-size: 14px;
	}
}

.jsExample{
	border-top: 0px;
	&:before{
		content: "JS Example";
		background-color: #fff;
		width: 80px;
		top: -11px;
		left: -11px;
	}
}

.codeExample{
	&:before{
		content: "Example";
		background-color: #f9f9f9;
		width: 70px;
		top: -1px;
		left: -1px;
	}
}

.codeBlock,
.exampleOutput{
	padding: 10px;
}

.exampleOutput{
	font-size: 1rem;
}

.codeBlock{
	background-color: #f9f9f9;
	border-top: 1px solid #ccc;
	border-bottom-left-radius: 4px;
	border-bottom-right-radius: 4px;
}

.docSwatch{
	min-height: 218.21px;
	border: 1px solid #ccc;
	padding: 10px 0 0 10px;
	font-size: 12px;
	margin-bottom: 5px;
}

.codeExample .line > div:after{
	content: attr(class);
	display: block;
	min-height: 40px;
	line-height: 40px;
	background-color: #EEE;
	text-align: center;
	border-radius: 3px;
	font-size: 12px;
}
// From Hologram Samples End

.codeExample pre{
	font-size: 0.875em;
	word-wrap: break-word;
}

.page-header-hdg{
	margin-top: 0;
}

.page-main{
	h1{
		border-bottom: 1px solid #ccc;
		&:first-child{
			margin-top: 0;
		}
	}
}

.list-modules-01{
	$border-color: #ccc;
	border: 1px solid $border-color;
	border-radius: 4px;
	list-style: none;
	margin: 0 0 20px;
	padding: 0;
	li{
		border-bottom: 1px solid $border-color;
		&:last-child{
			border-bottom-width: 0;
		}
		a{
			display: block;
			padding: 10px;
			color: $textcolor;
			text-decoration: none;
			&:hover,
			&:focus{
				background-color: #efefef;
			}
		}
	}
}
