.page-contents{
	@include outer-container;
	margin-bottom: 30px;
	.page-nav{
		@include span-columns(3);
	}
	.page-main{
		@include span-columns(9);
		@include omega();
	}
}

.page-header{
	margin-bottom: 30px;
	padding: 10px 0;
	background: rgba(0,0,0,0.6);
	&-content{
		@include outer-container;
	}
	.page-header-hdg{
		margin-bottom: 0;
		color: #fff;
		font-size: 20px;
		font-weight: normal;
	}
}

.page-footer{
	border-top: 1px solid #ccc;
	padding: 20px 0;
	color: #666;
	text-align: center;
}
